import { navLinks } from "@/config";
export default {
    data() {
        return {
            isLoading: true,
            isHome: true,
            menuOpen: false,
            navLinks: navLinks,
        };
    },
    mounted() {
        setTimeout(() => {
            this.isLoading = false;
        }, 3000);
    },
    watch: {
        menuOpen: function () {
            if (this.menuOpen) {
                document
                    .querySelector("body")
                    .setAttribute("style", "overflow:hidden");
            } else {
                document
                    .querySelector("body")
                    .setAttribute("style", "overflow:auto");
            }
        },
    },
    methods: {
        updateMenuStatus(bool) {
            this.menuOpen = bool;
        },
        changeMenuStatus() {
            this.menuOpen = !this.menuOpen;
        },
    },
};
